<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
<!-- <div>
    <h1>Hi {{ username }}</h1>
    <p>{{ secretContent }}</p>
  </div> -->
<v-form @submit.prevent="findNumber">
    <v-container>
      <v-row>
        <v-col cols="12">
          <v-text-field
            v-model="message"
            clearable
            label="Wyszukaj zbiornik"
            type="text"
          >
            <template v-slot:append>
              <v-fade-transition leave-absolute>
                <v-progress-circular
                  v-if="loading"
                  size="24"
                  color="info"
                  indeterminate
                ></v-progress-circular>
              </v-fade-transition>
            </template>
            <template v-slot:append-outer>
              <v-menu
                style="top: -15px"
                offset-y
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    v-bind="attrs"
                    v-on="on"
                    @click="findNumber(message)"
                  >
                    <v-icon left>
                      mdi-magnify
                    </v-icon>
                    Szukaj
                  </v-btn>
                </template>
              </v-menu>
            </template>
          </v-text-field>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
<div style="margin: 50px;" v-for="parcel in parcel_list" :key="parcel.id"  class="parcel" :id = "parcel.id">
  <div class="title">
        {{parcel.name}}
    </div>
<div class="slots">
  <div v-for="index in parcel.size" :key="index" >
    <div style="">
      <v-btn
                color="default"
                class="ma-2 slot"
                @click="openDialog($event, parcel.id, index)"
            >
      {{parcelSlot(parcel.slots, index)}}
      </v-btn>
    </div>
  </div>
</div>
</div>
<v-dialog
        v-if="dialog"
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
        scrollable
      >
        <v-card tile>
          <v-toolbar
            flat
            dark
            color="primary"
          >
            <v-btn
              icon
              dark
              @click="dialog = false; action = 'add'"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Zbiornik</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-card-text>
            <TankForm :formData="formData" :action="action" :parcelId="activeParcel" :slotId="activeSlot" @addTank="addHandle()"/>
          </v-card-text>
        </v-card>
      </v-dialog>
  </div>
</template>

<script>
import TankForm from '@/components/Home/TankForm.vue';
// @ is an alias to /src
// import Parcel from '@/components/Home/Parcel.vue';
import { mapState } from 'vuex';
import axios from 'axios';
// import $ from 'jquery';

export default {
  name: 'Home',
  components: {
    // Parcel,
    TankForm,
  },
  data() {
    return{
      message: 'Hey!',
      loading: false,
      secretContent: '',
      username: '',
      parcelOne: '',
      formData: '',
      dialog: false,
        activeParcel: '',
        activeSlot: '',
        action: 'add',
    }
  },
  beforeCreate() {
    this.$store.dispatch('common/loadParcelList');
  },
  created() {
    if (!this.$store.getters['authentication/isLoggedIn']) {
      this.$router.push('/login');
    }
    this.$store.dispatch('authentication/getSecretContent')
    .then(() => {
      this.secretContent = this.$store.getters['authentication/getSecretContent'];
    });
    this.username = this.$store.getters['authentication/getUser'].username;
/////////////////

  },
  mounted() {

  },
  methods: {
    clickMe () {
        this.loading = true
        this.message = 'Wait for it...'
        setTimeout(() => {
          this.loading = false
          this.message = `You've clicked me!`
        }, 2000)
      },
    findNumber(tankNumber) {
    this.loading = true;
    this.message = 'Szukam!';
    // let foundSlots = [];
    const buttonsInner = document.getElementsByClassName('slot');
    let iterations = buttonsInner.length;
    for(let slot of buttonsInner) {
      if(slot.innerText.includes(tankNumber)){
        slot.style.backgroundColor = 'yellow';
      }
       if (!--iterations) {
          this.loading = false;
          this.message = '';
        }
    }
    // console.log(buttonInner);
    // console.log(all);
    //   for(let parcel of this.parcel_list){
    //     parcel.slots.filter((slot) => {
    //       if(slot.name.includes(tankNumber)){
    //         foundSlots.push(slot.name);
    //         slot.background = 'white';
    //       }
    //     })
    //     if (!--iterations) {
    //       this.loading = false;
    //       this.message = '';
    //     }
    //   }
    },
    addHandle() {
// this.$store.dispatch('common/loadParcelList');
      this.dialog = false;
      // location.reload();
    },
    parcelSlot(slots, index){
      const result = slots.filter((el) => {
          if(el.slotId == index) {
            return el.name;
            // this.id =el.id;
          }
      });
      if(result.length){
        return result[0].name;
      }else{
        return '';
      }
    },
    openDialog(event, parcel, slot) {
      console.log(event.target.outerText);
      this.activeParcel = parcel;
      this.activeSlot = slot;
      if(event.target.outerText != ''){
        this.action = 'edit';
        this.parcel_list[parcel-1].slots.filter((el)=> {
          if(el.slotId == slot){
            axios.get(`${this.api_url}tank/get/${el.id}`, {
          headers: {
            Authorization: this.token,
          //   apitoken: context.rootState.apitoken,
          //   clientbrowser: Vue.clientbrowser,
          },
        })
        .then((result)=>{
          // Object.assign({}, this.tankData, result.data[0])
          this.formData = result.data[0];
          this.dialog = true;
        })

          }
        });
      }else {
        this.dialog = true;
      }

    },
  },
  computed: {
    ...mapState('common', [
      'parcel_list',
    ]),
    ...mapState('authentication', [
      'user',
      'token',
    ]),
    ...mapState([
      'api_url'
    ]),
  },
};
</script>
<style  lang="scss">
    #home {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));;
    }
    .parcel {
        .title{
            text-align: center;
            width: 100%;
        }
        .slots{
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            margin: 10px;
            .slot {
                text-align:center;
                background-color:#ccc;
                border: 1px solid #fff;
                cursor: pointer;
                &:hover{
                    background-color: #999;
                }
            }
        }
    }
    .ma-2{
      width:95% !important;
      text-transform: none !important;
    }
</style>